function addSM (map) {
  var url = 'http://121.248.96.215:8091/iserver/services/map-swmm/rest/maps/watershed1@swmm'
  SMsource = new ol.source.TileSuperMapRest({
    url: url,
    wrapX: true
  })
  SMlayer = new ol.layer.Tile({
    source: SMsource,
    projection: 'EPSG:4326'
  })
  SMlayer.setZIndex(3)
  map.addLayer(SMlayer)
}

function removeSMlayer (map) {
  map.removeLayer(SMlayer)
}

function getSource () {
  return SMsource
}

export default {
  // addWFS,
  addSM,
  removeSMlayer,
  getSource
}



